















import {
  Component,
  Model,
  Prop,
  Vue,
} from 'vue-property-decorator';
import { ITopicItem } from '@/types/interface';

@Component
export default class SubscribeLabelMap extends Vue {
  @Prop({ default: () => [] }) list!: ITopicItem[] | [];
  @Model('change', { default: () => [] }) readonly checked!: [string];

  showChecked(item: string) {
    return this.checked.indexOf(item) >= 0;
  }

  clickHandler(item: string) {
    const temp = JSON.parse(JSON.stringify(this.checked));
    const idx = temp.indexOf(item);
    if (idx < 0) temp.push(item);
    else temp.splice(idx, 1);
    this.$emit('change', temp);
  }
}
