




















import Swal from 'sweetalert2';
import { Action, State } from 'vuex-class';
import { Component, Vue } from 'vue-property-decorator';
import { ILoginPayload, ITopicItem } from '@/types/interface';
import Banner from '../../components/Banner.vue';
import BaseFooter from '../../components/BaseFooter.vue';
import SubscribeLabelMap from '../../components/SubscribeLabelMap.vue';

@Component({
  components: {
    Banner,
    BaseFooter,
    SubscribeLabelMap,
  },
})

export default class EditSubscribeItem extends Vue {
  @Action('getTopic', { namespace: 'subscription' }) getTopic!: () => void;
  @Action('getUserSub', { namespace: 'subscription' }) getUserSub!: () => Promise<void>;
  @Action('postUserSub', { namespace: 'subscription' }) postUserSub!: (list: string[] | []) => Promise<ILoginPayload>;
  @State('topicList', { namespace: 'subscription' }) topicList!: ITopicItem[] | [];
  @State('userSubList', { namespace: 'subscription' }) userSubList!: string[];

  listOne: string[] | [] = [];

  get isMemberProcess() {
    return this.$route.query.process === 'member';
  }

  link() {
    this.$router.push('/member');
  }

  next() {
    this.postUserSub(this.listOne)
      .then((data: ILoginPayload) => {
        if (data.Code === '0000') {
          if (this.isMemberProcess) {
            Swal.fire({
              toast: true,
              width: 300,
              position: 'top',
              showConfirmButton: false,
              showCloseButton: true,
              background: '#EDFAF5',
              buttonsStyling: false,
              customClass: {
                closeButton: 'success-toast-close-btn',
                popup: 'success-toast-popup',
                title: 'success-toast-title',
              },
              title: '儲存成功',
            });
          } else {
            this.$router.push('/main');
          }
        }
      });
  }

  created() {
    this.getTopic();
    this.getUserSub()
      .then(() => {
        if (this.userSubList.length !== 0) this.listOne = this.userSubList;
      });
  }
}
